  <template>
    <div class="custom-actions">
      <button class="btn btn--icon btn--small" @click="onAction('view-item', rowData, rowIndex)"><span class="icon-zoom-in"></span></button>
      <button class="btn btn--icon btn--small" @click="onAction('edit-item', rowData, rowIndex)"><span class="icon-draw"></span></button>
      <button class="btn btn--icon btn--small btn--negative" @click="onAction('delete-item', rowData, rowIndex)"><span class="icon-trash"></span></button>
      <button v-if="rowData.files && rowData.files.length > 0" class="btn btn--icon btn--small btn--success" @click="downloadAction" :disabled="downloadClicked"><span class="icon-download"></span></button>
    </div>
  </template>

<script>
export default {
  props: {
    rowData: {
      type: Object,
      required: true,
    },
    rowIndex: {
      type: Number,
    },
    onAction: {
      type: Function,
      required: true,
    },
  },
  data() {
    return {
      downloadClicked: false,
    };
  },
  methods: {
    downloadAction(event) {
      this.downloadClicked = true;
      this.onAction('download-item', this.rowData, this.rowIndex);
    },
  },
};
</script>

  <style>
.custom-actions {
  min-width: 120px;
}
</style>
